body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu-links{
    background-color: #e5e5e5;
    padding: 15px 0;
}
.menu-links a, .menu-links span {
 padding: 8px 15px;
 text-decoration: none;
 color: black;
 border-radius: 30px;
 display: inline-block;
}
.menu-links a:hover{

    text-decoration: none;
}
.menu-links a.active{
    background-color:#605ea9;
    color: white;
}
.header-menu{
    padding: 20px 0;
    background-color:#605ea9;
    color: white;
}
.header-menu a{
    color: #a7a7a7;
}
.menu{
    padding: 15px 0;
}
.menu img{
    height: 60px;
}
.menu .icon-sec .icon{
    color: black;
    padding-right: 5px;
    font-size: 28px;
}
.dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    text-align: left;
    position: absolute;
    background-color: #f9f9f9;
    width: 93%;
    max-height: 400px ;
    overflow-y: auto;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 10;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  .filtered-data{
    height: 70px;
    cursor: pointer;
  }
  .filtered-data:hover{
    background-color: #e0e0fb;
  }
  .filtered-data, .filtered-data div {
    padding: 2px;
  }
  .filtered-data-img{
    overflow: hidden;
  }
  .filtered-data-img img{
    width: 100%;
  }
  .filtered-data-text{
    font-size: 14px;
  }

.container{
    max-width: 1444px;
}
.text-shadow{
    text-shadow: 2px 2px 2px grey;
}
.primary-backgroun-color{
    background-color:#605ea9;
    color: white;
    padding: 30px 0;
}
.card{
    overflow: hidden;
}

.card .card-img img{
    width: 100%;
}
.card-text{
    padding: 15px 0;
}
.offer-zone{
    background-color: rgb(226, 226, 226);
    padding: 30px 0;
}
.text-ellipsis{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
a:hover{
    text-decoration: none;
}
.text-link{
    cursor: pointer;
}

.carousel-inner, .carousel-inner img, .carousel-item{
    height: 100%;
}
.localization-icon{
    padding: 10px;
    color: gray;
    font-size:13px;
}

@media screen and (min-width: 480px) {
    .card, .product-card .product-img {height: 300px}
  }

@media screen and (max-width: 480px) {
.card{height: 200px}
.product-card .product-img {height: 110px;}
}
.footer-menu, .footer-bottom{
    padding: 20px 0;
    background-color:#605ea9;
    color: white;
}

.footer-link{
    padding: 20px 0;
}

.footer-link a{
    padding: 5px 0;
    display: inherit;
    text-decoration: none;
    color: black;
}

.footer-bottom{
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    height: 400px;    
    background-color:#605ea9;
    color: white;
}

.footer-bottom .menu-links{
    background-color: transparent;
}

.footer-bottom .menu-links a{
    color: white !important;
}
.product-card {
    /* box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2); */
    
    transition: 0.3s;
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
    border: 1px solid lightgrey;
}
.product-container{
    padding: 10px 10px !important;
}
.product-card .product-img{
    overflow: hidden;
    text-align: center;
}
.product-card .product-img img{
    width: 100%;
}

.product-card .product-heading{
    font-size: 15px;
}

.product-card:hover {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.product-card .product-description{
    font-size: 14px;
}

.detailImg{
    overflow: hidden;
}
.detailImg img{
    height: 100%;
    width: 100%;
}
.link{
    color: blue;
    cursor: pointer;
}
